@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.Navbar {
    display: flex;
    width: 100%;
    height: 30px;
    padding: 0 0 30px 0;
    background: #ffffff;

    margin: -3px -3px 0 -3px;

    border: solid;
    border-width: 3px;
    border-color: #ffffff;
    box-shadow: 1px 1px 5px #6868684d;

    font-family: "Poppins";
    font-weight: 300;

    /* position: fixed; */
    z-index: 2;
}

.Navbar ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    margin-left: -35px;

    width: 100%; 
    height: auto;
}

.Navbar ul li {
    list-style-type: none;
    color: #000000;
    padding: 0 25px 0 25px;
}

.Navbar ul li a {
    text-decoration: none;
    color: #000000;
    font-size: 18px;
}

.github-link {
    margin-top: -7px;
    font-size: 30px;
}

.github-link a {
    color: #8B5FBF;
    text-decoration: none;
}

.github-link a:hover {
    color: #FEC0CE;
    transition: 0.15s;
}

.App {
  text-align: center;
  min-height: 100px;
}


body {
  background: #f5f5f5;
  /* background: #FEC0CE; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;

  color: #ffffff;
}
.Code pre {
    width: 80%;

    /* background: #8b5fbf;  */
    background: #ececec;
    background: radial-gradient(circle, #c0c0c0, 0%, #f1f1f1 100%);
    border-radius: 5px;
    border: none;

    box-shadow: 1px 3px 3px #80808062;

    margin: auto auto 20px auto;
    padding: 10px;
    display: block;
    white-space: pre-wrap;

    line-height: 28px;

    text-align: left;

    font-size: 18px;
    color: #000;
    outline: none;

    resize: none;
}
.Home {
    min-height: 100px;
    padding-top: 40px;

    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.project-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    width: 50vw; 
    margin: 0 auto 0 auto;
    display: flex;
    flex-wrap: nowrap;
    padding: 25px;
    align-items: center;
    justify-content: center;
}

.paragraph {
    width: 100%;

    display: flex;
    flex-wrap: wrap;
    margin-bottom: 35px;

    display: block;
    text-align: center;

    font-size: 19px;
}

.hex {
    position: absolute;
    z-index: -1;

    fill: blue;
}

.Code {
    min-width: 20vw;
}
.Terminal {
    background: #1e1e1e; 
    width: 40vw;
    height: 20vw;

    padding: 15px 15px 45px 15px;
    border-radius: 10px;

    position: relative;

    box-shadow: 3px 19px 7px #00000062;

    margin: auto auto 60px auto;
}

.Terminal pre {
    width: 100%;
    height: 100%;

    text-align: left;
    word-break: normal !important;
    word-wrap: normal !important;
    white-space: pre !important;
    line-height: 25px;

    overflow: hidden;

    margin: 0;

    font-size: 18px;
}

.Terminal input {
    width: 100%;
    height: 20px;

    border: none;
    outline: none;
    border-radius: 10px;

    font-family: 'Courier New', Courier, monospace;

    margin: 10px 0 0 -15px;

    font-size: 17px;
    padding: 15px;

    color: #ffffff;
    background: #1e1e1e
}

.titleBar {
    width: 100%;
    background: #dddddd;
    background: linear-gradient(#e3e3e3, #cfcfcf);

    padding: 5px 0 5px 0;
    top: 0;
    left: 0;

    color: #000000;

    font-family: "Source Sans Pro", sans-serif;
    font-weight: 600;
    font-size: 16px;

    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

    position: absolute;
    z-index: 1; 
}

.controls {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

    display: flex;
    justify-content: left;

    position: absolute;

    margin: 2px 0 0 10px;
}

.circle{
    width: 15px;
    height: 15px;

    margin: 0 4px 0 0;
}

.Project {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    max-width: 25vw;
    height: auto;

    padding: 15px;
    margin: 15px;

    justify-content: left;
    text-align: left;

    /* background: #9a6ecc; */
    background: #ececec;
    /* background: radial-gradient(circle, #9a6ecc 0%, #8b5fbf 100%); */
    background: radial-gradient(circle, #c0c0c0, 0%, #ebebeb 100%);

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 600;
    font-size: 18px;
    -webkit-font-smoothing: antialiased;

    color: #000;
    box-shadow: 1px 3px 3px #80808062;

    border-radius: 7px;
}

.language {
    font-size: 15px;
    align-items: center;
    display: flex;
}

.lang-icon {
    width:20px;
    height:20px;
    margin-right: 3px;
}

.project-title {
    padding: 5px;
    font-style: italic;
}

.project-desc {

}

.demo {
    /* color: #ffffff; */
    color: #000;
    margin: 10px;
    text-align: right;
    font-weight: bold;
}

.demo a {
    text-decoration: none;
    color: #1c2939;
}

.github {
    /* color: #ffffff; */
    color: #000;
    margin: 10px;
    text-align: right;
    font-weight: bold;
}

.github a {
    text-decoration: none;
    color: goldenrod;
}

.github a:hover {
    text-decoration: underline;
}

.github:hover {
    color: #a1a1a1;
}

.Section {
    /* background: #9198e5; */
    background: #ececec;
    /* background: radial-gradient(circle, #E3879E 0%, #8B5FBF 100%); */
    background: radial-gradient(circle, #c0c0c0, 0%, #f1f1f1 100%);

    box-shadow: 1px 1px 5px #96969662;

    color: #000;

    width: 100%;
    padding: 50px;
}

.Section .title {
    font-family: "Poppins";
